<template>
  <div>
    <vx-header :showback="1" :showdefault="1" :title="$t('settings.LED')" @default="reset"></vx-header>
    <div class="vx-container">
      <div class="setting_cont">
        <div class="progress-list">
          <vx-progress :title="light.title" :num="light.value" :prefix="unit" :irregular="false" @touchend="setLight" @update="val=>light.value=val" :range="light.range" :step="light.step"></vx-progress>
          <vx-progress :title="gamma.title" :num="gamma.value" :irregular="true" @touchend="setGamma" @update="gammaUpdate" :range="gamma.range" :step="gamma.step"></vx-progress>
          <div class="mask">{{$t('settings.colourTemperature')}}</div>
          <vx-progress :title="all.title" :custom="custom" :num="all.value" :prefix="unitk" :irregular="false" @touchend="setAll" @update="updateAll" :range="all.range" :step="all.step"></vx-progress>
          <vx-progress :title="r.title" class="pred" :num="r.value" :irregular="false" @touchend="setRGB" @update="val=>r.value=val" :range="r.range" :step="r.step"></vx-progress>
          <vx-progress :title="g.title" class="pgreen" :num="g.value" :irregular="false" @touchend="setRGB" @update="val=>g.value=val" :range="g.range" :step="g.step"></vx-progress>
          <vx-progress :title="b.title" class="pblue" :num="b.value" :irregular="false" @touchend="setRGB" @update="val=>b.value=val" :range="b.range" :step="b.step"></vx-progress>
          <div class="btns">
            <div class="m-btn yellow-btn" @click="submit">{{$t('settings.SolidifiedReceivingCard')}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";
  import { getRGBbyA } from './rgb';
  import { Toast, MessageBox } from 'mint-ui';
  export default {
    data() {
      return {
        ischange: false,
        unit: "%",
        unitk: "K",
        light: {
          title: this.$t('settings.screenLight'),
          value: 50,
          step: 1,
          range: {
            start: 0,
            end: 100
          }
        },
        gamma: {
          title: this.$t('settings.gamma'),
          value: 100,
          step: 10,
          range: {
            start: 100,
            end: 400
          }
        },
        all: {
          title: this.$t('settings.all'),
          value: 4000,
          step: 100,
          range: {
            start: 4000,
            end: 9500
          }
        },
        r: {
          title: this.$t('common.Rvalue'),
          value: 255,
          step: 1,
          range: {
            start: 0,
            end: 255
          }
        },
        g: {
          title: this.$t('common.Gvalue'),
          value: 51,
          step: 1,
          range: {
            start: 0,
            end: 255
          }
        },
        b: {
          title: this.$t('common.Bvalue'),
          value: 51,
          step: 1,
          range: {
            start: 0,
            end: 255
          }
        },
        gammaValue: 1,
        custom: true
      };
    },
    created() {
      this.initPage();
    },
    methods: {
      setLight() {
        this.ischange = true;
        htp.post(urls.setGlobalBrightness, {
          brightness: this.light.value
        }).then(res => {
          if(res.errorCode[0] === 20060201) {
            Toast(this.$t('settings.UnconnectedReceivingCard'));
          } else if(res.errorCode[0] === 20060202) {
            Toast(this.$t('settings.ReceivingCardExp'));
          }
        });
      },
      setGamma() {
        this.ischange = true;
        htp.post(urls.setGlobalGamma, {
          gamma: this.gammaValue
        }).then(res => {
          if(res.errorCode[0] === 20060201) {
            Toast(this.$t('settings.UnconnectedReceivingCard'));
          } else if(res.errorCode[0] === 20060202) {
            Toast(this.$t('settings.ReceivingCardExp'));
          }
        });
      },
      setAll() {
        this.ischange = true;
        this.custom = false;
        this.saveRGB();
      },
      saveRGB() {
        htp.post(urls.setGlobalRGB, {
          rValue: this.r.value,
          gValue: this.g.value,
          bValue: this.b.value
        }).then(res => {
          if(res.errorCode[0] === 20060201) {
            Toast(this.$t('settings.UnconnectedReceivingCard'));
          } else if(res.errorCode[0] === 20060202) {
            Toast(this.$t('settings.ReceivingCardExp'));
          }
        });
      },
      setRGB() {
        this.ischange = true;
        this.custom = true;
        this.saveRGB();
      },
      initPage() {
        htp.get(urls.getReceiverCardParam).then(res => {
          if(res.status === 10000000) {
            this.light.value = res.data.brightness;
            this.gamma.value = res.data.gamma * 100;
            this.r.value = res.data.rValue;
            this.g.value = res.data.gValue;
            this.b.value = res.data.bValue;
          } else {
            if(res.errorCode[0] === 20060201) {
              Toast(this.$t('settings.UnconnectedReceivingCard'));
            } else if(res.errorCode[0] === 20060202) {
              Toast(this.$t('settings.ReceivingCardExp'));
            }
          }
        });
      },
      gammaUpdate(current) {
        this.gamma.value = current;
        this.gammaValue = current / 100;
      },
      updateAll(current) {
        this.custom = false;
        this.all.value = current;
        let rgbValue = getRGBbyA(current);
        this.r.value = rgbValue.r;
        this.g.value = rgbValue.g;
        this.b.value = rgbValue.b;
      },
      sureConform() {
        this.ischange = true;
        this.light.value = 60;
        this.gamma.value = 280;
        this.gammaValue = 2.8;
        this.custom = true;
        this.r.value = 255;
        this.g.value = 255;
        this.b.value = 255;
        this.setGamma();
        this.setLight();
        this.saveRGB();
      },
      submit() {
        htp.post(urls.saveToReceiverCard, {
          gamma: this.gammaValue,
          brightness: this.light.value,
          rValue: this.r.value,
          gValue: this.g.value,
          bValue: this.b.value
        }).then(res => {
          this.ischange = false;
          if(res.status === 10000000) {
            Toast(this.$t('common.successful'));
          } else {
            if(res.errorCode[0] === 20060201) {
              Toast(this.$t('settings.UnconnectedReceivingCard'));
            } else if(res.errorCode[0] === 20060202) {
              Toast(this.$t('settings.ReceivingCardExp'));
            }
          }
        });
      },
      reset() {
        MessageBox.confirm('', {
          title: this.$t('common.tip'),
          message: this.$t('common.ledmsg'),
          confirmButtonText: this.$t('common.confirm'),
          cancelButtonText: this.$t('scenarist.cancel'),
          showCancelButton: true
        }).then(() => {
          this.sureConform();
        }, () => {
        });
      }
    },
    beforeRouteLeave(to, from, next) {
      if(this.ischange) {
        MessageBox.confirm('', {
          title: this.$t('common.tip'),
          message: this.$t('settings.isSolidifiedReceivingCard'),
          confirmButtonText: this.$t('common.confirm'),
          cancelButtonText: this.$t('scenarist.cancel'),
          showCancelButton: true
        }).then(() => {
          this.submit();
          next();
        }, () => {
          next();
        });
      } else {
        next();
      }
    }
  };
</script>


<style scoped lang="less">
  .vx-container {
    -webkit-overflow-scrolling: auto !important;
  }
  .setting_cont {
    margin-top: 25px;
    .vc-cell {
      border-top: 1px solid #4f5054;
      &:first-child {
        border-top: none;
      }
    }

    .vx-progress {
      border-bottom: 1px #4f5054 solid;
      background-color: #383940;
      padding-top: 20px;
      padding-bottom: 30px;
    }
    .progress-list {
      text-align: left;
      font-size: 24px;
      height: 60px;
      line-height: 60px;
      text-indent: 35px;
      .mask {
        color: #ddd;
        font-size: 24px;
      }
      .vx-progress {
        margin-top: 0;
        color: #a8a8aa;
      }
    }
  }
  .btns {
    display: flex;
    margin: 50px 100px;
    .m-btn {
      flex: 1;
      color: #ddd;
      height: 70px;
      line-height: 70px;
      background-color: #933e00;
      font-size: 34px;
      border-radius: 8px;
      text-align: center;
      text-indent: 0;
    }
    .mr {
      margin-right: 100px;
    }
  }
</style>




